.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.market-down {
  background: rgba(134, 33, 33, 0.3);
}

.market-up {
  background: rgba(20, 115, 47, 0.3);
}

.market {
  background: rgba(215, 197, 242, 0.3);
}

.background {
  height: 100%;
  width: 100%;
  position: absolute;
}

.background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgb(245, 245, 255)),
    to(rgb(153, 137, 178))
  );
  background: linear-gradient(rgb(245, 245, 255), rgb(153, 137, 178));
  -webkit-clip-path: circle(38% at 20% 5%);
  clip-path: circle(38% at 20% 5%);
}

.background::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgb(153, 137, 178)),
    to(rgb(255, 255, 255))
  );
  background: linear-gradient(rgb(153, 137, 178), rgb(245, 245, 255));
  -webkit-clip-path: circle(38% at 80% 100%);
  clip-path: circle(38% at 80% 100%);
}

.background.going-down::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgb(148, 8, 8)),
    to(rgb(228, 8, 8))
  );
  background: linear-gradient(rgb(148, 8, 8), rgb(228, 8, 8));
  -webkit-clip-path: circle(38% at 20% 5%);
  clip-path: circle(38% at 20% 5%);
}

.background.going-down::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgb(228, 8, 8)),
    to(rgb(148, 8, 8))
  );
  background: linear-gradient(rgb(228, 8, 8), rgb(148, 8, 8));
  -webkit-clip-path: circle(38% at 80% 100%);
  clip-path: circle(38% at 80% 100%);
}

.background.going-up::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgb(26, 134, 55)),
    to(rgb(20, 80, 13))
  );
  background: linear-gradient(rgb(26, 134, 55), rgb(20, 80, 13));
  -webkit-clip-path: circle(38% at 20% 5%);
  clip-path: circle(38% at 20% 5%);
}

.background.going-up::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgb(26, 134, 55)),
    to(rgb(20, 80, 13))
  );
  background: linear-gradient(rgb(20, 80, 13), rgb(26, 134, 55));
  -webkit-clip-path: circle(38% at 80% 100%);
  clip-path: circle(38% at 80% 100%);
}
